<template>
  <el-upload
    :limit="1"
    action="#"
    :file-list="fileList"
    :on-remove="handleRemove"
    :on-change="changeImage"
    :before-upload="beforeUpload"
    :http-request="upload"
  >
    <el-button
      round
      style="
        background:rgba(255, 255, 255, 0.5);
        font-weight:800;
        font-family:'仓耳渔阳体 W02
        border-radius:25px'
        "
    >
      上传文件
    </el-button>
  </el-upload>
</template>

<script>
import {
  uploadFile
} from "@/api/upload"
import PubSub from "pubsub-js"
export default {
  name: "CloudDiskUploadFile",
  props: {
    parentId: {
      type: [String, Number],
      default: "0"
    }
  },
  data() {
    return {
      fileList: []
    }
  },

  mounted() {

  },

  methods: {
    handleRemove(file, fileList) {
      console.log("删除回调", file, fileList)
    },
    changeImage(file, fileList) {
      console.log("改变回调", file, fileList)
    },
    beforeUpload(file) {
      console.log("上传前回调", file)
    },
    async upload(params) {
      console.log("上传中", params)
      if(params.file) {
        try {
          await uploadFile({
            parentId: this.parentId,
            file: params.file
          })
          this.$message.success("上传成功")
          PubSub.publish("updateList", this.parentId.toString())
        } catch (error) {
          this.$message.warning(error)
        }
      }
      this.fileList = []
      this.$router.go(0)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
