<template>
  <el-dialog
    :visible="showNewNameBox"
    width="400px"
    :modal="false"
    custom-class="dialog"
    :show-close="false"
  >
    <div
      slot="title"
    >
      <span style="font-size:20px;font-weight:800;font-family:'仓耳渔阳体 W02'">文件重命名</span>
    </div>
    <el-form :model="FormData">
      <el-form-item
        prop="name"
        label-width="60px"
      >
        <el-input
          v-model="FormData.name"
          placeholder="请输入文件名"
          style="width:250px"
        />
      </el-form-item>
    </el-form>
    <div
      slot="footer"
    >
      <el-row
        type="flex"
        justify="center"
        align="middle"
        style="width:400px;padding-right:60px;padding-bottom:20px"
      >
        <el-col style="width:100px">
          <el-button
            style="background:#000000;font-weight:800;font-family:'仓耳渔阳体 W02';color:#ffffff"
            @click="btnOK"
          >
            确认
          </el-button>
        </el-col>
        <el-col style="width:100px">
          <el-button
            style="background:#ffffff;font-weight:800;font-family:'仓耳渔阳体 W02';color:#000000"
            @click="btnCancel"
          >
            取消
          </el-button>
        </el-col>
      </el-row>
    </div>
  </el-dialog>
</template>

<script>
import PubSub from "pubsub-js"
import {
  newFileName
} from "@/api/getDocList"
export default {
  name: "CloudDiskFileNewName",
  props: {
    showNewNameBox: {
      type: Boolean,
      default: false
    },
    fileItem: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      FormData: {
        name: ""
      }
    }
  },
  crated() {

  },
  mounted() {
    this.FormData.name = this.fileItem.name
  },

  methods: {
    async btnOK() {
      console.log(this.fileItem.ID.toString(), this.fileItem.parentId.toString(), this.FormData.name)
      if(this.FormData.name === "") {
        return this.$message.warning("不能为空")
      }
      try {
        const res = await newFileName({
          id: this.fileItem.ID.toString(),
          parentId: this.fileItem.parentId.toString(),
          newName: this.FormData.name
        })
        console.log(res)
        if(res.status === 200) {
          this.$message.success(`已改名为${this.FormData.name}`)
        }
        this.FormData = {
          name: ""
        }
        PubSub.publish("updateList", this.fileItem.parentId.toString())
        this.btnCancel()
        PubSub.publish("closeOptions")
      } catch (error) {
        this.$message.warning("操作失败")
      }
    },
    btnCancel() {
      this.FormData = {
        name: this.fileItem.name
      }
      PubSub.publish("showNewNameBox")
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
