<template>
  <el-card style="background:rgba(0,0,0,0.3);border-radius:25px;border:0">
    <div class="optionBtnContainer">
      <!-- 下载 -->
      <div
        ref="download"
        class="optionBtn"
      >
        <el-button
          type="text"
          round
          style="color:#fff;font-size:25px"
          @click.stop="downloadDocument"
        >
          <i class="el-icon-download" />
        </el-button>
        <div
          v-if="download"
          class="btnText showAnimate"
        >
          下载
        </div>
      </div>
      <!-- 文件移动 -->
      <div
        ref="move"
        class="optionBtn"
      >
        <el-button
          type="text"
          round
          style="color:#fff;font-size:25px"
          @click.stop="fileMove"
        >
          <i class="el-icon-edit-outline" />
        </el-button>
        <div
          v-if="move"
          class="btnText showAnimate"
        >
          移动
        </div>
      </div>
      <!-- 重命名按钮 -->
      <div
        ref="rename"
        class="optionBtn"
      >
        <el-button
          type="text"
          style="color:#fff;font-size:25px"
          round
          @click.stop="newFileName"
        >
          <i class="el-icon-edit" />
        </el-button>
        <div
          v-if="rename"
          class="btnText showAnimate"
        >
          重命名
        </div>
      </div>
      <!-- 删除按钮 -->
      <div
        ref="del"
        class="optionBtn"
      >
        <el-button
          type="text"
          style="color:#fff;font-size:25px"
          round
          @click.stop="delFile"
        >
          <i class="el-icon-delete" />
        </el-button>
        <div
          v-if="del"
          class="btnText showAnimate"
        >
          删除
        </div>
      </div>
      <!-- 取消按钮 -->
      <div
        ref="cancel"
        class="optionBtn"
      >
        <el-button
          type="text"
          style="color:#fff;font-size:25px"
          round
          @click.stop="btnCancel"
        >
          <i class="el-icon-circle-close" />
        </el-button>
        <div
          v-if="cancel"
          class="btnText showAnimate"
        >
          取消
        </div>
      </div>
    </div>
    <el-progress
      v-if="percentage"
      :text-inside="true"
      :stroke-width="26"
      :percentage="percentage"
    />
  </el-card>
</template>

<script>
import {
  delFile
} from "@/api/getDocList"
import PubSub from "pubsub-js"
// import {
//   downloadDocument
// } from "@/api/getDocList"
import axios from "axios"
// import streamSaver from "streamsaver"
// import downloadjs from "downloadjs"
export default {
  name: "CloudDiskOptionList",
  props: {
    fileItem: {
      type: Object,
      required: true
    },
    name: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      percentage: 0,
      showDialog: false,
      download: false,
      move: false,
      del: false,
      rename: false,
      cancel: false
    }
  },

  mounted() {
    PubSub.subscribe("closeOptions", ()=>{
      console.log("关闭")
      this.btnCancel()
    })
    this.$refs.download.addEventListener("mouseenter", ()=>{
      this.download = true
    })
    this.$refs.download.addEventListener("mouseleave", ()=>{
      this.download = false
    })

    this.$refs.move.addEventListener("mouseenter", ()=>{
      this.move = true
    })
    this.$refs.move.addEventListener("mouseleave", ()=>{
      this.move = false
    })
    this.$refs.del.addEventListener("mouseenter", ()=>{
      this.del = true
    })
    this.$refs.del.addEventListener("mouseleave", ()=>{
      this.del = false
    })
    this.$refs.rename.addEventListener("mouseenter", ()=>{
      this.rename = true
    })
    this.$refs.rename.addEventListener("mouseleave", ()=>{
      this.rename = false
    })
    this.$refs.cancel.addEventListener("mouseenter", ()=>{
      this.cancel = true
    })
    this.$refs.cancel.addEventListener("mouseleave", ()=>{
      this.cancel = false
    })
  },

  methods: {
    btnCancel() {
      if(this.name === "fileList") {
        console.log(this.name)
        PubSub.publish("optionCancel")
      } else {
        PubSub.publish("optionCancelF")
      }
    },
    // 下载
    async downloadDocument() {
      // const res = await downloadDocument({
      //   name: this.fileItem.path.slice(57),
      //   fileName: this.fileItem.name.replaceAll(this.fileItem.ext, "")
      // })
      // console.log(res)
      const that = this
      const params = {
        name: this.fileItem.path.slice(57),
        fileName: this.fileItem.name.replaceAll(this.fileItem.ext, "")
      }
      const request = axios.create({
        baseURL: "http://106.15.58.0:8000/api",
        timeout: 50000,
        headers: {
          Authorization: `Bearer ${this.$store.state.user.token}`
        },
        onDownloadProgress: function(progressEvent) {
          console.log(progressEvent)
          let process = (progressEvent.loaded / progressEvent.total * 100)
          let progressText = `下载进度：${process}%`
          that.percentage = Math.floor(process)
          console.log(progressText)
        }
      })
      request({
        method: "get",
        url: "/file/download",
        responseType: "arraybuffer",
        params
      })
        .then(res => {
          const {
            data, headers
          } = res
          if(headers["content-disposition"]) {
            const str = headers["content-disposition"]
            const a = str.indexOf("filename=")
            const fileName = str.slice(a + 9).replaceAll("\"", "")
            console.log(a, str, fileName)

            const blob = new Blob([data], {
              type: headers["content-type"]
            })
            let dom = document.createElement("a")
            let url = window.URL.createObjectURL(blob)
            dom.href = url

            dom.download = decodeURI(fileName)
            console.log(dom)
            dom.style.display = "none"
            document.body.appendChild(dom)
            dom.click()
            dom.parentNode.removeChild(dom)
            window.URL.revokeObjectURL(url)
            // downloadjs(blob, fileName, headers["content-type"])
            this.btnCancel()
            this.$message.success("下载成功")
          }
          return res
        }, err=>{
          this.btnCancel()
          this.$message.warning("下载失败")
          return Promise.reject(err)
        })
    },
    // 删除文件 or 删除文件夹（未做）
    async delFile() {
      try {
        const res = await delFile({
          id: this.fileItem.ID.toString(),
          name: this.fileItem.path.slice(57)
        })
        console.log(res)
        PubSub.publish("updateList", this.fileItem.parentId.toString())
        this.$message.success("删除成功")
        this.btnCancel()
      } catch (error) {
        this.$message.warning("删除失败")
      }
    },
    // 重命名文件 or 文件夹
    newFileName() {
      console.log(1)
      PubSub.publish("showNewNameBox")
    },
    // 文件移动
    fileMove() {
      PubSub.publish("showFileMoveBox")
    }
  }
}
</script>

<style lang="less" scoped>
.showAnimate{
  animation: fadeIn;
  animation-duration: 1s;
}
.optionBtnContainer{
  display: flex;
  width: 400px;
  justify-content: center;
  align-items: center;
  .optionBtn{
    display: flex;
    width: 100px;
    height: 50px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .btnText{
      border-radius: 25px;
      font-size: 18px;
      padding: 2px 7px 2px 7px;
      background-color: black;
      color: #fff;
      font-family: '仓耳渔阳体 W02';
      position: absolute;
      top: 3px;
    }
  }
}

</style>
