import request from "@/utils/request"

export const getDocList = id => request({
  method: "GET",
  url: `/file/list/${id}`
})

export const getDirList = id => request({
  method: "GET",
  url: `/dir/list/${id}`
})

export const createList = data => request({
  method: "POST",
  url: "/dir/create",
  data
})

export const downloadDocument = params => request({
  method: "get",
  url: "/file/download",
  params
})

export const getDirListAll = () => request({
  url: "/dir/list/all"
})

export const delFile = data => request({
  url: "/file/delete",
  method: "delete",
  data
})

export const newFileName = data => request({
  url: "/file/rename",
  method: "put",
  data
})

export const fileMove = data => request({
  url: "/file/move",
  method: "put",
  data
})

export const delDir = id => request({
  url: `/dir/${id}`,
  method: "delete"
})

export const renameDir = data => request({
  url: "/dir/rename",
  method: "put",
  data
})
