<template>
  <el-dialog
    :visible="showDialog"
    width="400px"
    :modal="false"
    custom-class="dialog"
    :show-close="false"
  >
    <div
      slot="title"
    >
      <span style="font-size:20px;font-weight:800;font-family:'仓耳渔阳体 W02'">新建目录名</span>
    </div>
    <el-form :model="FormData">
      <el-form-item
        prop="name"
        label-width="60px"
      >
        <el-input
          v-model="FormData.name"
          placeholder="请输入目录名"
          style="width:250px"
        />
      </el-form-item>
    </el-form>
    <div
      slot="footer"
    >
      <el-row
        type="flex"
        justify="center"
        align="middle"
        style="width:400px;padding-right:60px;padding-bottom:20px"
      >
        <el-col style="width:100px">
          <el-button
            style="background:#000000;font-weight:800;font-family:'仓耳渔阳体 W02';color:#ffffff"
            @click="btnOK"
          >
            确认
          </el-button>
        </el-col>
        <el-col style="width:100px">
          <el-button
            style="background:#ffffff;font-weight:800;font-family:'仓耳渔阳体 W02';color:#000000"
            @click="btnCancel"
          >
            取消
          </el-button>
        </el-col>
      </el-row>
    </div>
  </el-dialog>
</template>

<script>
import {
  createList
} from "@/api/getDocList"
import PubSub from "pubsub-js"
export default {
  name: "CloudDiskNewFileList",
  props: {
    showDialog: {
      type: Boolean,
      default: false
    },
    parentId: {
      type: [String, Number],
      default: "0"
    }
  },
  data() {
    return {
      FormData: {
        name: ""
      }
    }
  },

  mounted() {

  },

  methods: {
    async btnOK() {
      try {
        console.log(this.parentId)
        const res = await createList({
          name: this.FormData.name,
          parentId: this.parentId.toString()
        })
        console.log(res)
        PubSub.publish("updateShowDialog")
        PubSub.publish()
        this.$message.success("新建目录成功")
        this.FormData = {
          name: ""
        }
        PubSub.publish("updateList", this.parentId.toString())
      } catch (error) {
        console.log(error)
        this.$message.warning("操作失败")
      }
    },
    btnCancel() {
      this.FormData = {
        name: ""
      }
      PubSub.publish("updateShowDialog")
    }
  }
}
</script>

<style lang="less" scoped>

.dialog{
  border-radius: 25px;
  overflow: hidden;
}
</style>
