<template>
  <el-card class="file-list-container">
    <div class="title unSelect_text">
      我的文件：
    </div>
    <!-- 导航区 -->
    <div class="fastTo unSelect_text">
      <div style="float:left;font-family:'仓耳渔阳体 W02';cursor:pointer">
        <span
          v-for="(item,index) in fileBar"
          :key="index"
          class="showAnimate"
          @click="barToFile(item.ID?item.ID:item.id)"
        >{{ item.name }}></span>
      </div>
      <!-- 删除文件夹 -->
      <div
        v-if="fileBar[1]"
        style="float:right"
      >
        <el-button
          style="background: rgba(255, 255, 255, 0.5);margin-left:5px;font-weight:800;font-family:'仓耳渔阳体 W02'"
          round
          @click="delDir"
        >
          删除文件夹
        </el-button>
      </div>
      <!-- current文件夹改名 -->
      <div
        v-if="fileBar[1]"
        style="float:right"
      >
        <el-button
          style="background:rgba(255,255,255,0.5);margin-left:5px;font-weight:800;font-family:'仓耳渔阳体 W02'"
          round
          @click="renameDir"
        >
          文件夹重命名
        </el-button>
      </div>
      <!-- 新建目录 -->
      <div style="float:right">
        <el-button
          style="background:rgba(255,255,255,0.5);margin-left:5px;font-weight:800;font-family:'仓耳渔阳体 W02'"
          round
          @click="(showDialog=true)"
        >
          新建目录
        </el-button>
      </div>
      <!-- 上传文件 -->
      <div style="float:right">
        <upload-file
          :parent-id="currentId"
        />
      </div>
    </div>

    <div
      v-if="fileList.length"
      class="hidden_over"
    >
      <div class="file_flex">
        <el-checkbox-group
          v-model="checkList"
          class="file_flex"
          text-color="#000000"
          @change="option"
        >
          <div
            v-for="(item,index) in fileList"
            :key="index"
            class="showAnimate"
            @click="addToBar(item)"
          >
            <el-checkbox
              :label="item"
              class="checkBox"
              border
            >
              <file-card
                :id="item.ID"
                :file-name="item.name"
                :parent-id="item.parentId"
                :ext="item.ext?item.ext:''"
              />
            </el-checkbox>
          </div>
        </el-checkbox-group>
      </div>
    </div>
    <!-- 三级路由 -->
    <router-view v-else />

    <!-- // 目录命名框 -->
    <new-file-list
      :show-dialog="showDialog"
      :parent-id="currentId"
    />

    <!-- // 显示操作按钮框 -->
    <div
      v-if="showOptions"
      class="mark"
      @click.self="close"
    >
      <div
        class="optionContainer showAnimate"
      >
        <option-list
          :file-item="fileItem"
          :name="optionName"
        />
      </div>
      <fileNewName
        :show-new-name-box="showNewNameBox"
        :file-item="fileItem"
      />
      <fileMove
        :show-file-move-box="showFileMoveBox"
        :file-item="fileItem"
      />
    </div>

    <!-- // 目录改名框 -->
    <el-dialog
      :visible="showNewDirNameBox"
      width="400px"
      :modal="false"
      custom-class="dialog"
      :show-close="false"
    >
      <div
        slot="title"
      >
        <span style="font-size:20px;font-weight:800;font-family:'仓耳渔阳体 W02'">文件夹重命名</span>
      </div>
      <el-form :model="FormData">
        <el-form-item
          prop="name"
          label-width="60px"
        >
          <el-input
            v-model="FormData.name"
            placeholder="请输入新文件夹名"
            style="width:250px"
          />
        </el-form-item>
      </el-form>
      <div
        slot="footer"
      >
        <el-row
          type="flex"
          justify="center"
          align="middle"
          style="width:400px;padding-right:60px;padding-bottom:20px"
        >
          <el-col style="width:100px">
            <el-button
              style="background:#000000;font-weight:800;font-family:'仓耳渔阳体 W02';color:#ffffff"
              @click="btnOK"
            >
              确认
            </el-button>
          </el-col>
          <el-col style="width:100px">
            <el-button
              style="background:#ffffff;font-weight:800;font-family:'仓耳渔阳体 W02';color:#000000"
              @click="btnCancel"
            >
              取消
            </el-button>
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
import fileNewName from "./components/fileNewName"
import fileMove from "./components/fileMove.vue"
import optionList from "./components/optionList.vue"
import uploadFile from "@/components/uploadFile"
import fileCard from "./components/fileCard.vue"
import newFileList from "./components/newFileList.vue"
import PubSub from "pubsub-js"
import {
  getDirList, getDocList, getDirListAll, delDir, renameDir
} from "@/api/getDocList"

// import {
//   fileList
// } from "@/assets/test"
export default {
  name: "ClouddiskIndex",
  components: {
    fileCard,
    uploadFile,
    newFileList,
    optionList,
    fileNewName,
    fileMove
  },
  data() {
    return {
      // 文件列表
      fileList: [],
      // 快速移动导航栏
      fileBar: [
        {
          name: "根目录",
          id: "0",
          parentId: "0"
        }
      ],
      FormData: {
        name: ""
      },
      showNewDirNameBox: false, // 显示文件夹改名框
      showFileMoveBox: false, // 显示文件移动框
      showNewNameBox: false, // 显示更改文件名框or目录名
      showDialog: false, // 显示更改目录名框
      showOptions: false,  // 显示操作按钮框
      checkList: [],  // 当前选中文件
      fileItem: null, // 用来传递当前选中文件
      optionName: ""  // 操作页面名，用来区别是fileList还是folder
    }
  },
  computed: {
    currentId() {
      return this.fileBar[this.fileBar.length - 1].ID
    }
  },
  watch: {
    "$route": {
      async handler(newValue) {
        PubSub.publish("optionCancel")
        PubSub.publish("optionCancelF")
        if(newValue.fullPath === "/fileList") {
          this.updateFileList("0")
          this.fileBar = this.fileBar.slice(0, 1)
        } else {
          this.fileList = []
          let ind = 0
          this.fileBar.forEach((item, index)=>{
            if(item.ID == newValue.params.parentId) {
              ind = index
            }
          })
          // console.log(ind)
          // console.log(this.$route)
          // console.log(ind)
          if(ind !== 0) {
            this.fileBar = this.fileBar.slice(0, ind + 1)
            ind = 0
          } else {
            // this.fileBar.push(...fileList.filter(item=>item.id === newValue.params.parentId))
            // const {
            //   data
            // } = await getDirList(newValue.params.parentId)
            // console.log(1, data)
            const dirList = await getDirListAll()
            console.log(dirList.data.data)
            console.log("加入", dirList.data.data.filter(item=>item.ID == newValue.params.parentId))
            this.fileBar.push(...dirList.data.data.filter(item=>item.ID == newValue.params.parentId))
          }
        }
        const newSet = new Set(this.fileBar)
        this.fileBar = [...newSet]
      }
    }
  },
  created() {
    if(this.$route.path === "/fileList") {
      this.updateFileList("0")
      this.fileBar = [
        {
          name: "根目录",
          id: "0",
          parentId: "0"
        }
      ]
    } else {
      this.fileList = []
    }
  },
  // 初始化
  mounted() {
    console.log("fileBar", this.fileBar[this.fileBar.length - 1])
    // 显示文件移动页面
    PubSub.subscribe("showFileMoveBox", ()=>{
      this.showFileMoveBox = !this.showFileMoveBox
    })
    // 显示改名页面
    PubSub.subscribe("showNewNameBox", ()=>{
      this.showNewNameBox = !this.showNewNameBox
    })
    // 关闭操作页面
    PubSub.subscribe("optionCancel", ()=>{
      this.showOptions = false
      this.checkList = []
    })
    // 展示文件命名弹出层
    PubSub.subscribe("updateShowDialog", ()=>{
      this.showDialog = !this.showDialog
    })
    // 路由页面操作页面
    // eslint-disable-next-line no-unused-vars
    PubSub.subscribe("optionShow", (a, val)=>{
      this.fileItem = val[0]
      console.log(222, this.fileItem)
      this.optionName = "folder"
      this.showOptions = true
    })
    // eslint-disable-next-line no-unused-vars
    PubSub.subscribe("updateFileList", (a, item)=>{
      this.addToBar(item)
    })
    // eslint-disable-next-line no-unused-vars
    PubSub.subscribe("updateList", (a, parentId)=>{
      this.updateFileList(parentId)
    })
    if(window.localStorage.getItem("FILE_BAR")) {
      this.fileBar = JSON.parse(window.localStorage.getItem("FILE_BAR"))
    }
    window.addEventListener("beforeunload", ()=>{
      window.localStorage.setItem("FILE_BAR", JSON.stringify(this.fileBar))
    })
  },
  methods: {
    addToBar(item) {
      if(item && item.ext) {
        return
      } else {
        try {
          console.log("更新文件列表引入参数", item)
          this.fileBar.push(item)
          const newSet = new Set(this.fileBar)
          this.fileBar = [...newSet]
          this.updateFileList(item.ID)
          this.$router.push({
            name: "folder",
            params: {
              parentId: item.ID
            }
          })
        } catch (error) {
          console.log(error)
          this.$message.warning("加载失败")
        }
      }
    },
    async updateFileList(parentId) {
      this.fileList = []
      // const arr = fileList.filter(item=>item.parentId === parentId)
      const res1 = await getDirList(parentId)
      const res2 = await getDocList(parentId)
      console.log("后端返回", res1, res2)
      // console.log(data, arr)

      // this.fileList = [...res1.data.data, ...res2.data.data]
      console.log("列表赋值中")
      if(res1.data.data.length !== 0 && res2.data.data.length !== 0) {
        this.fileList = [...res1.data.data, ...res2.data.data]
      } else if (res1.data.data.length !== 0 || res2.data.data.length !== 0) {
        this.fileList = res1.data.data.length !== 0 ? [...res1.data.data] : [...res2.data.data]
      } else {
        this.fileList = []
      }
      console.log("列表赋值完成", this.fileList)
    },
    barToFile(id) {
      console.log(id)
      console.log(this.fileBar)
      if(id === "0") {
        this.$router.push("/fileList")
        return
      }
      this.$router.push({
        name: "folder",
        params: {
          parentId: id
        }
      })
    },
    option(val) {
      this.fileItem = val[0]
      console.log(111, this.fileItem)
      this.optionName = "fileList"
      this.showOptions = true
    },
    // 关闭悬浮操作区
    close() {
      PubSub.publish("closeOptions")
    },
    // 删除文件夹
    async delDir() {
      try {
        const res = await delDir(this.fileBar[this.fileBar.length - 1].ID.toString())

        console.log("删除回调", res)
        if(res.data.msg === "存在子目录") {
          this.$message.warning("只能删除空文件夹")
        } else {
          if(this.fileBar[this.fileBar.length - 1].parentId == "0") {
            this.$router.push("/fileList")
          } else {
            this.$router.push({
              name: "folder",
              params: {
                parentId: this.fileBar[this.fileBar.length - 1].parentId.toString()
              }
            })
          }
          this.$message.success("删除成功")
        }
      } catch (error) {
        this.$message.warning("操作失败")
      }
    },
    // 重命名文件夹
    renameDir() {
      this.showNewDirNameBox = true
    },
    async btnOK() {
      try {
        if(this.FormData.name) {
          const dir = this.fileBar[this.fileBar.length - 1]
          const res = await renameDir({
            "id": dir.ID.toString(),
            "parentId": dir.parentId.toString(),
            "newName": this.FormData.name
          })
          console.log(res)
          if(res.status === 200) {
            this.$message.success(`已改为${this.FormData.name}`)
            this.fileBar[this.fileBar.length - 1].name = this.FormData.name
            this.FormData.name = ""
            this.showNewDirNameBox = false
          }
        } else {
          this.$message.warning("重命名不能为空")
        }
      } catch (error) {
        this.$message.warning("操作失败")
        this.showNewDirNameBox = false
      }
    },
    btnCancel() {
      this.showNewDirNameBox = false
      this.FormData.name = ""
    }
  }
}
</script>

<style lang="less" scoped>
.showAnimate{
  animation: fadeIn;
  animation-duration: 0.5s;
}
.file-list-container{
    margin: 20px;
    padding-bottom: 20px;
    flex: 1;
    height: 98vh;
    box-sizing: border-box;
    border-radius: 25px;
    box-shadow: none;
    background: rgba(255, 255, 255, 0.5);

    border: 0;
    .title{
        margin: 30px;
        box-sizing: border-box;
        font-size: 30px;
        font-weight: 900;
        font-family: '仓耳渔阳体 W02';
    }
    .fastTo{
        margin: 30px;
    }
    .hidden_over{
        width:100%;
        height: 75vh;
        overflow: hidden;
        .file_flex{
            padding-bottom: 20px;
            width:76vw;
            height: 75vh;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;
            overflow: auto;
            .checkBox{
              margin:5px 10px 10px 5px;
              padding: 5px 10px 10px 5px;
              width:120px;
              height:150px;
              border-radius: 25px;
            }
        }
    }
}
.mark{
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  .optionContainer{
    margin: 0;
    padding: 0;
    position: fixed;
    bottom: 20px;
    left: 50vw;
    transform: translateX(-50%);
  }
}

</style>
