<template>
  <el-dialog
    :visible="showFileMoveBox"
    width="400px"
    :modal="false"
    custom-class="dialog"
    :show-close="false"
  >
    <div
      slot="title"
    >
      <span style="font-size:20px;font-weight:800;font-family:'仓耳渔阳体 W02'">文件移动（直接确认移动至根目录）</span>
    </div>
    <!-- 树形结构 -->
    <el-tree
      :data="data"
      :props="defaultProps"
      @node-click="handleNodeClick"
    />
    <!-- 底部区域 -->
    <div
      slot="footer"
    >
      <el-row
        type="flex"
        justify="center"
        align="middle"
        style="width:400px;padding-right:60px;padding-bottom:20px"
      >
        <el-col style="width:100px">
          <el-button
            style="background:#000000;font-weight:800;font-family:'仓耳渔阳体 W02';color:#ffffff"
            @click="btnOK"
          >
            确认
          </el-button>
        </el-col>
        <el-col style="width:100px">
          <el-button
            style="background:#ffffff;font-weight:800;font-family:'仓耳渔阳体 W02';color:#000000"
            @click="btnCancel"
          >
            取消
          </el-button>
        </el-col>
      </el-row>
    </div>
  </el-dialog>
</template>

<script>
import {
  fileMove
} from "@/api/getDocList"
import PubSub from "pubsub-js"
import changeToTree from "@/utils/changeToTree"
import {
  getDirListAll
} from "@/api/getDocList"
export default {
  name: "CloudDiskFileMove",
  props: {
    showFileMoveBox: {
      type: Boolean,
      default: false
    },
    fileItem: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "name"
      },
      moveTo: null
    }
  },

  mounted() {
    this.getDirListAll()
  },

  methods: {
    btnCancel() {
      PubSub.publish("showFileMoveBox")
    },
    async btnOK() {
      try {
        const res = await fileMove({
          "id": this.fileItem.ID.toString(),
          "fileName": this.fileItem.name,
          "parentId": this.fileItem.parentId.toString(),
          "newParentId": this.moveTo ? this.moveTo.ID.toString() : "0"
        })
        console.log(res)
        this.$message.success(`已移动至"${this.moveTo ? this.moveTo.name : "根目录"}"`)
        PubSub.publish("updateList", this.fileItem.parentId.toString())
        PubSub.publish("closeOptions")
        PubSub.publish("showFileMoveBox")
      } catch (error) {
        this.$message.warning("移动失败")
      }
    },
    handleNodeClick(data) {
      this.moveTo = data
      console.log(this.moveTo)
    },
    async getDirListAll() {
      const {
        data
      } =  await getDirListAll()
      this.data = data.data
      this.data = changeToTree(this.data, 0)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
