<template>
  <div class="file-card">
    <img
      class="file_icon"
      :src="iconSrc"
    >
    <div class="file_name">
      {{ fileName }}
    </div>
  </div>
</template>

<script>
export default {
  name: "FileCard",
  props: {
    fileName: {
      required: true,
      type: String
    },
    id: {
      required: true,
      type: [String, Number]
    },
    parentId: {
      required: true,
      type: [String, Number]
    },
    ext: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      iconSrc: require("@/assets/img/icon/v1.png")
    }
  },

  mounted() {
    if(!this.ext) {
      this.iconSrc = require("@/assets/img/icon/file.png")
    } else {
      this.iconSrc = require("@/assets/img/icon/v11.png")
    }
  },

  methods: {

  }
}
</script>

<style lang="less" scoped>

.file-card{
    width: 120px;
    height: 150px;
    .file_icon{
        width:100px;
        -webkit-user-select:none;
        -moz-user-select:none;
        -o-user-select:none;
        -ms-user-select:none;
        user-select:none;
    }
    .file_name{
        margin-top: 10px;
        width: 100px;
        font-size: 14px;
        text-align: center;
        font-weight: 900;
        font-family: '仓耳渔阳体 W02';
        display: -webkit-box;
        overflow: hidden;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
    }
}
</style>
